.basketItems{
    background-color: #455442;
    border-radius: 10px;
    color: white;
    text-shadow: 1px 1px 2px #000 ;
    padding-top: 5px;
}
.basketItem:not(:first-of-type){
    padding-top: 20px;
}
.basketItem{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.basketItem h2{
    min-width: 100px;
    padding-left: 10px;
    font-size: 18px;
}
.counterContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100px;
}
.counterContainer span{
    font-size: 20px;
}
.counterContainer button:not(.counterBtn-one):active{
    transform: scale(0.9);
    font-size: 23px;
}

.counterBtn{
    padding: 5px;
    font-size: 25px;
    background-color: chocolate;
    border: 1px solid #000;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    color: #000;
    border-radius: 50%
}
.counterBtn-one{
    background-color: transparent;
    color: gray;
    border-color: gray
}
.basketItem img{
    width: 300px;
    height: 200px;
}
.basketItem__price{
    display: flex;
    align-items: center;
    min-width: 250px;
    justify-content: space-between;
}
.basketItem__price span{
    font-size: 18px;
}

.basketItem__trashIcon{
    color:rgba(255, 0, 0, 0.548);
    cursor: pointer;
    margin-right: 10px;
    width: 25px;
}
.emptyBasket{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    height: 200px;
    margin-top: 40px;
}
.totalContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 10px;
    border-top: 1px solid #000;
    margin-top: 5px;
}
.totalContainer span{
    color: rgb(0, 180, 0);
    text-shadow: 1px 1px 1px #000;
}
.confirmBtn{
    color:aliceblue;
    text-shadow: 1px 1px 1px #000;
    background-color: green;
    padding: 10px;
    border-radius: 10px;
}
.confirmBtn:hover{
    text-decoration: underline;
}
.confirmBtn:active{
    transform: scale(0.9);
    text-decoration: none;
}
.basketContainer{
    display: flex;
    flex-direction: column;
}
.clearBasketBtn {
    font-size: 18px;
    padding: 10px;
    background-color: #D2691E;
    border: 1px solid #000;
    border-radius: 10px;
}
.clearBasketBtn:hover {
    transform: scale(0.95);
}
.clearBasketBtn:active {
    background-color: red;
}
.clearBasketContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin: 10px 0;
}
.clearBasketContainer span{
    font-size: 18px;
}
.adaptiveCounterContainer{
    display: flex;
    justify-content: space-between;
    width: 40%;
}
.adaptiveNameContainer{
    display: flex;
    align-items: center;
    width: 60%;
    gap: 20%;
}


@media screen and (max-width:1100px) {
    .basketItem__price{
        min-width: 200px;
    }
}
@media screen and (max-width:900px) {
    .basketItem__price{
        min-width: 150px;
    }
    .adaptiveNameContainer{
        gap:10%;
    }
}
@media screen and (max-width:750px) {
    .adaptiveCounterContainer{
       flex-direction: column;
       gap: 30px;
       width: 25%;
       align-items: center;
    }
    .basketItem__trashIcon{
        margin: 0;
    }
}
@media screen and (max-width:650px) {
    .basketItem img{
        width: 250px;
        height: 180px;
    }
    .basketItem__price{
        flex-direction: column;
        align-items: center;
        min-width: max-content;
        justify-content: space-between;
        gap: 10px;
    }
}
@media screen and (max-width:550px) {
    .adaptiveNameContainer{
        gap:10px;
    }
}
@media screen and (max-width:500px) {
    .adaptiveNameContainer{
        flex-direction: column;
    }
}
@media screen and (max-width:415px) {
    .basketItem img{
        width: 200px;
    }
    .basketItem {
        justify-content: center;
        gap: 30px;
    }
}
